.stock-levels {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.stock-levels .summary-cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.stock-levels .summary-cards-row .summary-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
  gap: 1rem;
}
.stock-levels .summary-cards-row .summary-card .card-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.stock-levels .summary-cards-row .summary-card .card-content .card-value {
  font-size: 1.875rem;
  font-weight: 700;
  color: #111827;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.stock-levels .summary-cards-row .summary-card .card-content .card-value.warning {
  color: #f59e0b;
}
.stock-levels .summary-cards-row .summary-card .card-content .card-value.danger {
  color: #ef4444;
}
.stock-levels .summary-cards-row .summary-card .card-content .card-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.stock-levels .summary-cards-row .summary-card.total-stock .card-icon {
  background-color: #dbeafe;
  color: #3b82f6;
}
.stock-levels .summary-cards-row .summary-card.low-stock .card-icon {
  background-color: #fef3c7;
  color: #f59e0b;
}
.stock-levels .summary-cards-row .summary-card.out-of-stock .card-icon {
  background-color: #fee2e2;
  color: #ef4444;
}
.stock-levels .summary-cards-row .summary-card.total-value .card-value {
  color: #10b981;
}
.stock-levels .control-bar {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.stock-levels .control-bar .search-controls {
  display: flex;
  gap: 1rem;
  align-items: center;
  flex: 1;
}
.stock-levels .control-bar .search-controls .search-container {
  position: relative;
  min-width: 300px;
}
.stock-levels .control-bar .search-controls .search-container .search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  color: #9ca3af;
}
.stock-levels .control-bar .search-controls .search-container .search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: #f9fafb;
}
.stock-levels .control-bar .search-controls .search-container .search-input:focus {
  outline: none;
  border-color: #3b82f6;
  background-color: white;
}
.stock-levels .control-bar .search-controls .search-container .search-input::placeholder {
  color: #9ca3af;
}
.stock-levels .control-bar .search-controls .location-dropdown {
  position: relative;
  min-width: 150px;
}
.stock-levels .control-bar .search-controls .location-dropdown .location-select {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: white;
  appearance: none;
  cursor: pointer;
}
.stock-levels .control-bar .search-controls .location-dropdown .location-select:focus {
  outline: none;
  border-color: #3b82f6;
}
.stock-levels .control-bar .search-controls .location-dropdown .dropdown-arrow {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  color: #6b7280;
  pointer-events: none;
}
.stock-levels .control-bar .action-buttons {
  display: flex;
  gap: 1rem;
}
.stock-levels .control-bar .action-buttons .btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}
.stock-levels .control-bar .action-buttons .btn.btn-primary {
  background-color: #3b82f6;
  color: white;
}
.stock-levels .control-bar .action-buttons .btn.btn-primary:hover {
  background-color: #2563eb;
}
.stock-levels .control-bar .action-buttons .btn.btn-primary svg {
  color: white;
}
.stock-levels .inventory-table-container {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  overflow: hidden;
}
.stock-levels .inventory-table-container .inventory-table {
  width: 100%;
  border-collapse: collapse;
}
.stock-levels .inventory-table-container .inventory-table thead {
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}
.stock-levels .inventory-table-container .inventory-table thead th {
  padding: 1rem;
  text-align: left;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  border-bottom: 1px solid #e5e7eb;
}
.stock-levels .inventory-table-container .inventory-table tbody tr {
  border-bottom: 1px solid #f3f4f6;
}
.stock-levels .inventory-table-container .inventory-table tbody tr:last-child {
  border-bottom: none;
}
.stock-levels .inventory-table-container .inventory-table tbody tr:hover {
  background-color: #f9fafb;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td {
  padding: 1rem;
  font-size: 0.875rem;
  color: #111827;
  vertical-align: top;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.part-number {
  font-weight: 600;
  color: #3b82f6;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.part-name .name-primary {
  font-weight: 600;
  color: #111827;
  margin-bottom: 0.25rem;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.part-name .name-secondary {
  font-size: 0.75rem;
  color: #6b7280;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.location {
  font-weight: 500;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.bin {
  color: #9ca3af;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.on-hand, .stock-levels .inventory-table-container .inventory-table tbody tr td.reserved, .stock-levels .inventory-table-container .inventory-table tbody tr td.available {
  font-weight: 600;
  text-align: center;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.status .status-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.status .status-badge.in-stock {
  background-color: #d1fae5;
  color: #065f46;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.status .status-badge.low-stock {
  background-color: #fef3c7;
  color: #92400e;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.status .status-badge.out-of-stock {
  background-color: #fee2e2;
  color: #991b1b;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.value {
  font-weight: 600;
  color: #10b981;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.last-counted {
  color: #6b7280;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.actions .action-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  background: none;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.actions .action-btn:hover {
  background-color: #f3f4f6;
  border-color: #9ca3af;
  color: #374151;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.actions .action-btn.edit:hover {
  background-color: #dbeafe;
  border-color: #3b82f6;
  color: #3b82f6;
}
.stock-levels .inventory-table-container .inventory-table tbody tr td.actions .action-btn svg {
  width: 1rem;
  height: 1rem;
}
.stock-levels .fab {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}
.stock-levels .fab:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.stock-levels .fab svg {
  color: white;
}

@media (max-width: 1024px) {
  .stock-levels .control-bar {
    flex-direction: column;
    align-items: stretch;
  }
  .stock-levels .control-bar .search-controls {
    flex-direction: column;
    align-items: stretch;
  }
  .stock-levels .control-bar .search-controls .search-container {
    min-width: auto;
  }
  .stock-levels .control-bar .search-controls .location-dropdown {
    min-width: auto;
  }
  .stock-levels .control-bar .action-buttons {
    justify-content: center;
  }
  .stock-levels .inventory-table-container {
    overflow-x: auto;
  }
  .stock-levels .inventory-table-container .inventory-table {
    min-width: 800px;
  }
}
@media (max-width: 768px) {
  .stock-levels {
    padding: 1rem;
  }
  .stock-levels .summary-cards-row {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
  }
  .stock-levels .summary-cards-row .summary-card {
    padding: 1rem;
  }
  .stock-levels .summary-cards-row .summary-card .card-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  .stock-levels .summary-cards-row .summary-card .card-content .card-value {
    font-size: 1.5rem;
  }
  .stock-levels .summary-cards-row .summary-card .card-content .card-label {
    font-size: 0.75rem;
  }
  .stock-levels .control-bar {
    padding: 1rem;
  }
  .stock-levels .control-bar .action-buttons {
    flex-direction: column;
  }
  .stock-levels .control-bar .action-buttons .btn {
    width: 100%;
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .stock-levels .summary-cards-row {
    grid-template-columns: 1fr;
  }
  .stock-levels .control-bar .search-controls .search-container .search-input {
    padding: 0.75rem 1rem 0.75rem 2.5rem;
  }
}
